.FriendShip-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: start;
    width: 80%;
    height: 100%;
    -webkit-overflow-scrolling: touch;

    @media (max-width: 900px) {
        height: calc(100% - 60px - 54px);
    }
}

.FriendShip-img {
    position: relative;
    width: 240px;
    height: 150px;
    margin: 15px;
    border-radius: 2px;
    box-shadow: 5px 5px 5px grey;
    object-fit: cover;
    cursor: pointer;

    @media (max-width: 300px) {
        margin: 15px 0;
        width: 100%;
    }
}

.FriendShip-img:hover {
    right: 3px;
    bottom: 3px;
}

.img__modal--container {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

.img__modal {
    z-index: auto;
    max-width: 70%;
    max-height: 70%;
}

.blank-img {
    width: 240px;
    height: 150px;
    margin: 15px;
}