.navbar {
  position: fixed;
  z-index: 1000;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  padding: 0px 40px;

@media (max-width: 900px) {
  height: 60px;
  padding: 15px;
}

.navbar__logo {
  z-index: 1001;
  width: 120px;
  height: 60px;

  @media (max-width: 900px) {
    width: 100px;
    height: 50px;
  }
}

.navbar__logo--img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.navbar__menu--pc {
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 900px) {
    position: absolute;
    visibility: hidden;
    overflow: hidden;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
}

.navbar__menu--pc ul {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 0;
  list-style: none;
}

.navbar__menu--pc ul li a{
  text-decoration: none;
  color: white;
  font-size: 22px;
  font-weight: 600;
  transition: color 0.4s ease-in-out;
  cursor: pointer;
  position: relative;
}

.navbar__menu--pc ul li {
  padding: 15px;
}

.navbar__menu--pc a:hover {
  color: #5C518B;
  transition: color 0.4s ease-in-out;
}

/* 드롭다운 버튼 */
.navbar__menu--pc ul li:nth-child(6) {
  border: 1.5px solid rgb(131, 82, 134, 0.8);
  border-radius: 50px;
  padding: 5px 15px;
  font-family: "Lora";
  margin: 0 5px;
  font-size: 18px;
  color: #835286;
  background-color: white;
  cursor: pointer;
  position: relative;
  transition: all 0.4s ease-in-out;
}

.navbar__menu--pc ul li:nth-child(6):hover {
  transition: all 0.4 ease-in-out;
}


.navbar__menu--pc ul li:nth-child(7) {
  border: 1.5px solid rgb(86, 127, 187, 0.8);
  border-radius: 50px;
  padding: 5px 15px;
  font-family: "Lora";
  margin: 0 5px;
  font-size: 18px;
  color: #567FBB;
  background-color: white;
  cursor: pointer;
  position: relative;
  transition: all 0.4 ease-in-out;
}

.navbar__menu--pc ul li:nth-child(7):hover {
  transition: all 0.4 ease-in-out;
}

.dropdown-box {
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  top: calc(100% + 3px);
  left: 0;
  border-radius: 20px;
  z-index: 1000; 
}

.dropdown-box a:nth-child(1) {
  width: 100%;
  padding: 0px 0;
  cursor: pointer;
  white-space: nowrap;
  border: 1.5px solid rgb(131, 82, 134, 0.5);
  border-radius: 15px 15px 10px 10px;
  text-align: center;
  background-color: #FBF3FF;
  margin-bottom: 1px;
  color: #835286;
  font-size: 16px;
}

.dropdown-box a:nth-child(1) li {
  padding: 4px;
}

.dropdown-box a:nth-child(2) {
  width: 100%;
  cursor: pointer;
  white-space: nowrap;
  border: 1.5px solid rgb(131, 82, 134, 0.5);
  border-radius: 10px 10px 15px 15px;
  text-align: center;
  background-color: white;
  color: #835286;
  font-size: 16px;
}

.dropdown-box a:nth-child(2) li {
  padding: 4px;
}

.dropdown-box a:hover {
  opacity: 0.9; 
}

/* 모바일 네브바 */

#menu-toggle {
  display: none;
}

.menu-button {
  z-index: 1000;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 100%;
  cursor: pointer;
}

.menu-button .line {
  display: block;
  width: 100%;
  height: 3.5px;
  border-radius: 10px;
  background-color: #fff;
}

.menu-button .line1 {
  transform-origin: 0% 0%;
  margin-bottom: 6px;
  transition: transform 0.3s ease-in-out;
}

.menu-button .line2 {
  transition: transform 0.2s ease-in-out;
}

.menu-button .line3 {
  transform-origin: 0% 100%;
  margin-top: 6px;
  transition: transform 0.3s ease-in-out;
}

#menu-toggle:checked+.menu-button .line1 {
  transform: rotate(45deg);
}

#menu-toggle:checked+.menu-button .line2 {
  transform: scaleY(0);
}

#menu-toggle:checked+.menu-button .line3 {
  transform: rotate(-45deg);
}

.navbar__menu--mobile {
  display: none;
}

@media (max-width: 900px) {
  .menu-button {
    display: flex;
  }

  .navbar__menu--mobile {
    position: absolute;
    z-index: 500;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: start;
    width: 100vw;
    height: calc(var(--vh, 1vh) * 100);
    background-color: #DFE7FF;
    transition: left 0.5s;

    @media (max-height: 400px) {
      align-items: flex-start;
      justify-content: center;
      overflow-y: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
    }
  }

  #menu-toggle~.navbar__menu--mobile {
    left: 100vw;
  }

  .navbar__menu--mobile.open {
    left: 0;
  }

  #menu-toggle:checked~.navbar__menu--mobile {
    left: 0;
    transition: left 0.5s;
  }

  .navbar__menu--mobile ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;
    margin: 0;
    padding: 0;
  }

  .navbar__menu--mobile li {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 60vw;
    height: auto;
    margin: 10px;
    padding: 5px 20px;
    background-color: white;
    border-radius: 17px 17px 0px 17px;
    list-style: none;
    cursor: pointer;
    box-shadow: 2px 2px #a6adc0;

    @media (max-width: 600px) {
      width: 80vw;
    }
  }

  .navbar__menu--mobile a {
    width: 80vw;
    color: #787f96;
    font-size: 19px;
    font-weight: 700;
    text-decoration: none;
  }

